.reviews {
  display: grid;
  grid-template-columns: 1fr 1fr;
  font-family: $font-deafult;
  font-size: 14px;
  line-height: 28px;
  font-weight: 400;
  letter-spacing: -0.41px;
  font-style: normal;
  text-align: left;

  @media (max-width: $tablet-width-only) {
    grid-template-columns: 1fr 1fr;
    justify-items: center;
  }
}

.reviews__image-wrapper {
  grid-area: 1 / 1 / 2 / 3;

  @media (max-width: $tablet-width-only) {
    grid-area: 1 / 1 / 2 / 3;
  }
}

.reviews__image {
  width: 430px;
  height: 596px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: auto 100%;

  @media (max-width: $tablet-width-only) {
    width: auto;
    height: 415px;
  }

  @media (max-width: $mobile-width-only) {
    width: auto;
  }
}

.reviews__wrapper {
  margin-top: 113px;
  margin-left: -32px;
  padding: 30px 30px 22px 30px;
  align-self: start;
  background-color: $basic-white;
  grid-area: 1 / 2 / 2 / 4;

  @media (max-width: $tablet-width-only) {
    grid-area: 1 / 1 / 2 / 3;
    margin: 80px 193px 22px 193px;
  }

  @media (max-width: $mobile-width-only) {
    margin: 53px 26px 26px 26px;
    padding: 30px 21px 22px 21px;
  }
}

.reviews__content {
  @include reset-list;
}

.reviews__title {
  @include reset-list;
  margin-bottom: 34px;
  position: relative;
  font-family: $font-deafult;
  font-size: 30px;
  line-height: 37px;
  font-weight: 500;
  letter-spacing: -0.69px;
  font-style: normal;
  text-align: left;
  color: $basic-charade;

  &::after {
    content: "";
    position: absolute;
    bottom: -13px;
    left: 0;
    width: 68px;
    height: 1px;
    background-color: $basic-bittersweet;
  }

  @media (max-width: $mobile-width-only) {
    font-size: 20px;
    line-height: 24px;
    margin-top: 0;
    margin-bottom: 32px;
    text-align: center;

    &::after {
      content: "";
      position: absolute;
      bottom: -12px;
      left: 78px;
      width: 68px;
      height: 1px;
      background-color: $basic-bittersweet;
    }
  }
}

.reviews__text {
  @include reset-list;
  margin-bottom: 29px;
  color: $special-charade-07;

  @media (max-width: $mobile-width-only) {
    text-align: center;
    margin-bottom: 18px;
  }
}

.reviews__author {
  display: flex;
  flex-direction: row;
  align-items: center;

  @media (max-width: $mobile-width-only) {
    flex-direction: column;
  }
}

.reviews__author-name {
  font-family: $font-deafult;
  font-size: 16px;
  line-height: 20px;
  font-weight: 500;
  letter-spacing: -0.47px;
  font-style: normal;
  text-align: left;
  padding-left: 17px;
  color: $basic-charade;

  @media (max-width: $mobile-width-only) {
    padding-left: 6px;
  }
}
