.countrys {
  @include wrapper;
  padding-bottom: 82px;
  font-family: $font-deafult;
  font-size: 16px;
  line-height: 28px;
  letter-spacing: -0.47px;
  font-weight: 400;
  font-style: normal;
  text-align: center;
  color: transparentize($basic-charade, 0.3);
  display: flex;
  flex-direction: column;

  @media (max-width: $tablet-width-only) {
    padding-bottom: 83px;
    align-items: center;
  }

  @media (max-width: $mobile-width-only) {
    padding-bottom: 27px;
  }
}

.countrys__title {
  @include reset-list;
  position: relative;
  margin-bottom: 65px;
  width: 360px;
  font-size: 55px;
  line-height: 80px;
  letter-spacing: -1.25px;
  font-weight: 600;
  font-style: normal;
  text-align: left;
  color: $basic-charade;

  &::after {
    content: "";
    position: absolute;
    bottom: -33px;
    left: 0;
    width: 228px;
    height: 4px;
    background-color: $basic-bittersweet;
  }

  @media (max-width: $tablet-width-only) {
    margin-bottom: 21px;
    font-size: 30px;
    line-height: 45px;
    text-align: center;
    letter-spacing: 0;

    &::after {
      content: "";
      display: none;
    }
  }

  @media (max-width: $mobile-width-only) {
    font-size: 24px;
    line-height: 36px;
    width: 288px;
  }
}

.countrys__text {
  @include reset-list;
  margin-bottom: 54px;
  width: 330px;
  text-align: left;

  @media (max-width: $tablet-width-only) {
    margin-bottom: 39px;
    text-align: center;
  }

  @media (max-width: $mobile-width-only) {
    margin-bottom: 17px;
    width: 288px;
  }
}

.countrys__list {
  @include reset-list;
  display: grid;
  grid-template-columns: 350px 350px 350px;
  grid-template-rows: 1fr 1fr;
  row-gap: 101px;
  justify-content: space-between;

  @media (max-width: $desktop-width-only) {
    column-gap: 10px;
    grid-template-columns: 288px 288px 288px;
  }

  @media (max-width: $tablet-width-only) {
    grid-template-columns: 288px 288px;
    grid-template-rows: 1fr 1fr;
    column-gap: 29px;
    row-gap: 20px;
  }

  @media (max-width: $mobile-width-only) {
    display: flex;
    flex-direction: column;
    row-gap: 35px;
  }
}

.countrys__link {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  width: 350px;
  height: 430px;
  font-family: $font-deafult;
  font-size: 14px;
  line-height: 17px;
  font-weight: 500;
  font-style: normal;
  text-align: center;
  background-repeat: no-repeat;
  background-size: cover;

  &:hover .country__focus,
  &:focus .country__focus {
    display: flex;
  }

  &:active {
    opacity: 0.7;
  }

  @media (max-width: $tablet-width-only) {
    width: 288px;
    height: 425px;
  }

  @media (max-width: $mobile-width-only) {
    @include reset-list;
  }
}

.countrys__item--montenegro {
  margin-right: 60px;
  grid-area: 2 / 1 / 3 / 3;
  justify-self: center;

  @media (max-width: $tablet-width-only) {
    @include reset-list;
    grid-area: auto;
  }
}

.countrys__item--croatia {
  margin-left: 60px;
  grid-area: 2 / 2 / 3 / 4;
  justify-self: center;

  @media (max-width: $tablet-width-only) {
    @include reset-list;
    margin-top: 10px;
    grid-area: 3 / 1 / 3 / 3;
  }

  @media (max-width: $mobile-width-only) {
    @include reset-list;
  }
}
