.prices-item {
  display: flex;
  flex-direction: column;
  padding: 113px 0 20px 0;
  font-family: $font-deafult;
  font-size: 16px;
  line-height: 27px;
  font-weight: 400;
  font-style: normal;
  text-align: left;
  color: $special-charade-03;

  @media (max-width: $tablet-width-only) {
    padding: 18px 0 27px 0;
    flex-grow: 1;
    order: 1;
    text-align: center;
    width: 100%;
  }

  @media (max-width: $mobile-width-only) {
    padding: 18px 0 37px 0;
  }
}

.prices-item__title {
  @include reset-list;
  margin-bottom: 49px;
  position: relative;
  font-size: 55px;
  line-height: 90px;
  font-weight: 600;
  color: $basic-charade;

  &::after {
    content: "";
    position: absolute;
    bottom: -18px;
    left: 0;
    width: 175px;
    height: 3px;
    background-color: $basic-bittersweet;
  }

  @media (max-width: $tablet-width-only) {
    margin-bottom: 20px;
    font-size: 30px;
    line-height: 45px;

    &::after {
      content: "";
      display: none;
    }
  }

  @media (max-width: $mobile-width-only) {
    font-size: 24px;
    line-height: 36px;
  }
}

.prices-item__text {
  @include reset-list;
  margin-bottom: 60px;

  @media (max-width: $tablet-width-only) {
    margin-bottom: 33px;
    text-align: center;
    padding: 0 21px;
  }

  @media (max-width: $mobile-width-only) {
    margin-bottom: 18px;
  }
}

.prices-item__tel {
  @include reset-list;
  display: flex;
  flex-direction: column;
  font-size: 22px;
  line-height: 34px;
  font-weight: 500;
  color: $special-woodsmoke;
}

.prices-item__tel-message {
  text-align: center;
  margin-left: 12px;
  margin-bottom: 8px;

  @media (max-width: $tablet-width-only) {
    @include reset-list;
    margin-bottom: 10px;
    font-size: 18px;
  }
}

.prices-item__tel-number {
  font-size: 20px;
  line-height: 34px;
  color: $basic-black;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: baseline;
  margin-right: 34px;

  @media (max-width: $tablet-width-only) {
    @include reset-list;
    text-transform: uppercase;
    color: $basic-bittersweet;
    font-size: 18px;
    justify-content: center;
  }
}

.prices-item__tel-icon {
  fill: none;
  color: $basic-bittersweet;
  stroke: $basic-bittersweet;
  margin-top: 3px;
  margin-left: 4px;

  @media (max-width: $tablet-width-only) {
    display: none;
  }
}
