.modal {
  position: fixed;
  display: none;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: $special-charade-03;
  cursor: pointer;
  overflow: hidden;

  &--show {
    display: block;
  }
}

.modal__block {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 10;
  display: flex;
  justify-content: flex-end;
  width: 728px;
  min-height: 444px;
  font-family: $font-deafult;
  font-size: 25px;
  line-height: 43px;
  font-weight: 500;
  font-style: normal;
  text-align: center;
  color: $special-abbey;
  background-color: $special-soft-peach;
  background-image: url("../img/modal-bg.jpg");
  background-repeat: no-repeat;
  background-size: auto 100%;
  transform: translate(-50%, -50%);
  overflow: hidden;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/modal-bg-2x.jpg");
  }

  &--show {
    display: flex;
  }

  @media (max-width: $tablet-width-only) {
    width: 320px;
    min-height: 568px;
    background-image: none;
  }
}

.modal__wrapper {
  width: 390px;
  height: 100%;
  padding: 72px 20px 20px 18px;
  overflow: hidden;

  @media (max-width: $tablet-width-only) {
    width: 100%;
    padding: 100px 16px 20px 16px;
  }
}

.modal__form {
  @media (max-width: $tablet-width-only) {
    width: 100%;
  }
}

.modal__title {
  font-size: 25px;
  line-height: 43px;
  font-weight: 500;
}

.modal__input {
  width: 269px;
  margin-bottom: 25px;
  padding: 16px 15px 15px 14px;
  font-family: $font-deafult;
  font-size: 14px;
  line-height: 17px;
  color: $basic-charade;
  border: 1px solid $special-charade-03;

  &--tel {
    padding: 16px 15px 15px 50px;

    background-image: url("../img/input-ph.svg");
    background-repeat: no-repeat;
    background-position: 14px 5px;
    background-size: auto 29px;
  }

  &::placeholder {
    font-family: $font-deafult;
    font-size: 14px;
    line-height: 17;
    font-weight: 400;
    color: $basic-charade;
  }

  &:invalid {
    border: 1px solid $basic-bittersweet;
  }

  &:hover,
  &:focus {
    border: 1px solid $basic-charade;
  }

  @media (max-width: $tablet-width-only) {
    margin-bottom: 20px;
    width: 100%;
  }
}

.modal__button {
  width: 176px;
  padding: 18px 10px 17px 10px;
  border: 1px solid $basic-white;
  margin: 0 auto;

  @media (max-width: $tablet-width-only) {
    width: 288px;
  }
}

.modal__success {
  position: absolute;
  display: none;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 10px 120px;
  background-color: $special-soft-peach;

  &--show {
    display: flex;
  }
}

.modal__close {
  position: absolute;
  z-index: 5;
  top: 10px;
  right: 10px;
  width: 42px;
  height: 42px;
  background-color: transparent;
  background-image: url("../img/icon-close.svg");
  background-repeat: no-repeat;
  background-position: 10px 10px;
  border: none;
  cursor: pointer;
}
