.page-header {
  @include wrapper;
  display: flex;
  align-items: center;
  padding: 37px 0 31px 0;
  font-family: $font-deafult;
  font-size: 18px;
  line-height: 32px;
  font-style: normal;
  text-align: left;
  color: $special-woodsmoke;

  @media (max-width: $tablet-width-only) {
    margin: 0;
    padding: 18px 21px 15px 21px;

    &--no-js .page-header__toggle {
      display: none;
    }

    &--close .page-header__nav {
      display: none;
    }

    &--open .page-header__toggle {
      background-color: transparent;
      background-image: url("../img/icon-close.svg");
      background-position: 25px 14px;
    }
  }

  @media (max-width: $mobile-width-only) {
    width: auto;
    padding: 18px 50px 15px 16px;

    &--no-js {
      display: flex;
      flex-direction: column;
      padding: 18px 20px 15px 20px;
    }

    &--no-js .page-header__logo {
      margin-bottom: 15px;
    }

    &--no-js .page-header__nav {
      @include reset-list;
      width: 100%;
    }

    &--open .page-header__toggle {
      background-position: 13px 14px;
    }
  }
}

.page-header__logo {
  display: flex;
  align-items: center;
  margin-top: -6px;

  @media (max-width: $tablet-width-only) {
    margin-top: 0;
    align-items: flex-start;
  }
}

.page-header__logo-img {
  @media (max-width: $tablet-width-only) {
    width: 44px;
    height: auto;
  }
}

.page-header__toggle {
  display: none;

  @media (max-width: $tablet-width-only) {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 6;
    padding: 0;
    width: 72px;
    height: 50px;
    background-color: $basic-bittersweet;
    background-image: url("../img/icon-menu.svg");
    background-repeat: no-repeat;
    background-position: 21px 10px;
    border: none;
    cursor: pointer;
  }

  @media (max-width: $mobile-width-only) {
    width: 50px;
    background-position: 10px 10px;
  }
}

.page-header__nav {
  margin-top: -3px;
  margin-left: auto;
}

.page-header__tel {
  display: flex;
  align-items: center;
  margin-right: 14px;
  margin-left: 60px;
  justify-content: space-between;

  color: $special-woodsmoke;
  text-decoration: underline;

  stroke: $special-woodsmoke;

  &:hover,
  &focus {
    color: $basic-bittersweet;
    stroke: $basic-bittersweet;
  }

  &:active {
    opacity: 0.7;
  }

  @media (max-width: $tablet-width-only) {
    display: none;
  }
}

.page-header__tel-icon {
  margin-right: 16px;

  fill: none;
}
