.offers {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-right: 109px;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  font-style: normal;
  text-align: left;
  color: $basic-charade;

  @media (max-width: $tablet-width-only) {
    @include reset-list;
    padding: 0 47px 0 47px;
    order: 2;
    text-align: center;
    justify-content: space-around;
  }

  @media (max-width: $mobile-width-only) {
    flex-direction: column;
    padding: 0 15px 0 15px;
    row-gap: 30px;
  }
}

.offers__item {
  padding: 25px 30px 30px 30px;
  width: 370px;
  display: flex;
  flex-direction: column;
  box-shadow: 0 0 0 1px $special-charade-03;
  border-radius: 6px;

  &:hover,
  &:focus {
    box-shadow: 0 0 0 3px $basic-bittersweet;
  }

  &:hover .offers__title,
  &:focus .offers__title {
    color: $basic-bittersweet;
  }

  &:hover .offers__text::after,
  &:focus .offers__text::after {
    background-color: $basic-bittersweet;
    opacity: 0.5;
  }

  &:hover .offers__list-item::before,
  &:focus .offers__list-item::before {
    background-image: url("../img/icon-checkmark-black.svg");
  }

  @media (max-width: $tablet-width-only) {
    @include reset-list;
    width: 288px;
    justify-items: space-around;
  }
}

.offers__item--complex {
  margin-right: 30px;

  @media (max-width: $tablet-width-only) {
    @include reset-list;
  }
}

.offers__title {
  @include reset-list;
  margin-top: 15px;
  margin-bottom: 43px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-size: 28px;
  line-height: 34px;
  font-weight: 600;
  font-style: normal;
  text-align: left;
  color: $special-woodsmoke-07;

  @media (max-width: $tablet-width-only) {
    margin-top: 32px;
    margin-bottom: 12px;
    font-size: 24px;
    flex-direction: column;
    align-items: center;
  }
}

.offers__cost {
  font-size: 25px;
  line-height: 34px;
  font-weight: 600;

  @media (max-width: $tablet-width-only) {
    margin-top: 10px;
  }
}

.offers__cost--economical {
  margin-top: 4px;
  margin-right: 6px;

  @media (max-width: $tablet-width-only) {
    margin-top: 8px;
    margin-right: 13px;
  }
}

.offers__title--economical {
  margin-top: 16px;
  margin-bottom: 38px;
  letter-spacing: -1.27px;

  @media (max-width: $tablet-width-only) {
    margin-top: 32px;
    margin-bottom: 12px;
    flex-direction: column;
    align-items: center;
  }
}

.offers__text {
  @include reset-list;
  margin-bottom: 59px;
  margin-right: 20px;
  position: relative;
  color: $special-woodsmoke-07;

  &::after {
    content: "";
    position: absolute;
    bottom: -28px;
    left: 0;
    width: 310px;
    height: 1px;
    background-color: $special-charade-03;
  }

  @media (max-width: $tablet-width-only) {
    @include reset-list;
    padding: 0 20px 0 20px;

    &::after {
      content: "";
      position: absolute;
      bottom: -8px;
      left: 21px;
      width: 248px;
      height: 1px;
      background-color: $special-charade-03;
    }
  }
}

.offers__list {
  @include reset-list;
  padding: 0 20px 0 29px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-size: 16px;
  line-height: 32px;
  font-weight: 400;
  font-style: normal;
  text-align: left;

  @media (max-width: $tablet-width-only) {
    padding: 31px 22px 0 50px;
  }
}

.offers__list-item {
  margin-bottom: 15px;
  position: relative;
  color: $basic-charade;

  &::before {
    content: "";
    position: absolute;
    top: 5px;
    left: -30px;
    width: 22px;
    height: 22px;

    background-image: url("../img/icon-checkmark-focus.svg");
    background-repeat: no-repeat;
  }

  @media (max-width: $tablet-width-only) {
    margin-bottom: 10px;
  }
}

.offers__list--complex {
  margin-bottom: 37px;

  @media (max-width: $tablet-width-only) {
    margin-bottom: 34px;
  }

  @media (max-width: $mobile-width-only) {
    margin-bottom: 19px;
  }
}

.offers__list--economical {
  margin-bottom: 13px;

  @media (max-width: $tablet-width-only) {
    margin-bottom: 20px;
  }
}

.offers__list-item--complex:first-child {
  margin-bottom: 6px;

  @media (max-width: $tablet-width-only) {
    margin-bottom: 4px;
  }
}

.offers__button {
  @media (max-width: $tablet-width-only) {
    width: 288px;
    padding: 20px 39px 17px 40px;
  }
}
