.page-footer {
  @include wrapper;
  display: flex;
  flex-wrap: wrap;
  flex-grow: 0;
  padding-bottom: 74px;
  font-family: $font-deafult;
  font-size: 14px;
  line-height: 17px;
  font-weight: 400;
  font-style: normal;
  text-align: left;
  color: transparentize($special-woodsmoke, 0.5);

  @media (max-width: $tablet-width-only) {
    padding: 0 23px 50px 22px;
  }

  @media (max-width: $mobile-width-only) {
    padding: 0 13px 50px 16px;
  }
}

.page-footer__travels {
  display: flex;
  align-self: start;
  align-content: flex-start;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 0 20px 0 13px;
  border-right: 1px solid $special-mischka;
  width: 400px;

  @media (max-width: $tablet-width-only) {
    width: 395px;
    margin-bottom: 19px;
    padding: 0 15px 0 0;
  }

  @media (max-width: $mobile-width-only) {
    margin-bottom: 28px;
    padding-bottom: 8px;
    text-align: center;
    justify-content: center;
    border-right: none;
    border-bottom: 1px solid $special-mischka;
  }
}

.page-footer__title {
  @include reset-list;
  width: 100%;
  margin-bottom: 21px;
  font-size: 18px;
  line-height: 32px;
  font-weight: 600;
  color: $special-woodsmoke;

  @media (max-width: $mobile-width-only) {
    justify-items: center;
    margin-bottom: 20px;
  }
}

.page-footer__title--contacts {
  margin-bottom: 14px;
  margin-top: 1px;
  margin-left: 2px;

  @media (max-width: $mobile-width-only) {
    margin-bottom: 13px;
  }
}

.page-footer__list {
  @include reset-list;
  margin-right: 50px;

  &:nth-of-type(3n) {
    margin-right: 0;
  }

  @media (max-width: $tablet-width-only) {
    margin-right: 46px;
  }

  @media (max-width: $mobile-width-only) {
    @include reset-list;
  }
}


.page-footer__list--first {
  @media (max-width: $mobile-width-only) {
    margin-right: 58px;
    margin-bottom: 10px;
  }
}

.page-footer__list--second {
  @media (max-width: $mobile-width-only) {
    margin-left: 20px;
  }
}

.page-footer__list-item {
  display: block;
  margin-bottom: 10px;
  color: transparentize($special-woodsmoke, 0.5);

  &:hover,
  &foter {
    text-decoration: underline;
  }

  &:active {
    color: $special-woodsmoke;
  }
}

.page-footer__contacts {
  display: flex;
  flex-direction: column;
  align-self: start;
  align-content: flex-start;
  margin-right: 21px;
  margin-left: auto;
  padding: 0 20px 0 20px;

  @media (max-width: $tablet-width) {
    margin-right: 0;
    margin-left: 28px;
    padding: 0 22px 0 20px;
  }

  @media (max-width: $mobile-width-only) {
    margin: 0 auto;
    text-align: center;
    padding: 0 18px 0 3px;
  }
}

.page-footer__contacts-item {
  display: flex;
  margin-bottom: 17px;
  color: $basic-bittersweet;
  text-decoration: underline;

  &--mail {
    font-size: 14px;
    line-height: 17px;
    display: block;
    margin-bottom: 19px;

    @media (max-width: $tablet-width-only) {
      display: none;
    }
  }

  &--text {
    display: none;

    @media (max-width: $tablet-width-only) {
      display: block;
    }
  }

  &--tel {
    font-size: 16px;
    line-height: 32px;
  }

  &:hover,
  &:focus {
    color: transparentize($special-woodsmoke, 0.3);
  }

  &:active {
    opacity: 0.7;
  }
}

.page-footer__contacts-icon {
  &--mail {
    margin-right: 11px;
    margin-top: 4px;
    fill: $special-mischka;
  }

  &--tel {
    margin-right: 17px;
    margin-left: 6px;
    fill: none;
    stroke: $special-mischka;
  }
}


.page-footer__block-message {
  display: flex;
  flex-direction: column;
  align-self: start;
  align-content: flex-start;
  padding-left: 49px;
  border-left: 1px solid $special-mischka;
  width: 379px;

  @media (max-width: $tablet-width-only) {
    text-align: center;
    border-left: none;
    border-top: 1px solid $special-mischka;
    width: 100%;
    padding: 30px 0 0 0;
  }

  @media (max-width: $tablet-width-only) {
    padding: 29px 18px 0 14px;
  }

  @media (max-width: $tablet-width-only) {
    padding: 29px 0;
  }
}

.page-footer__text {
  @include reset-list;
  line-height: 24px;
  width: 320px;

  @media (max-width: $tablet-width-only) {
    width: 100%;
  }
}
