.country__greece {
  background-image: url("../img/greece-link.jpg");

  @media (min-resolution: 2dppx) {
    background-image: url("../img/greece-link-2x.jpg");
  }

  @media (max-width: $tablet-width-only) {
    background-position: -55px center;
  }
}

