.country__focus {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  display: none;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  color: $basic-white;
  background-color: transparentize($basic-charade, 0.3);
}

.country__message {
  display: block;
  width: 266px;
  padding: 30px 25px;
  text-transform: uppercase;
  border: 1px solid $basic-white;
}

.country__name {
  @include reset-list;
  position: relative;
  z-index: 1;
  width: 215px;
  padding: 17px 15px 16px 15px;
  font-size: 24px;
  line-height: 29px;
  font-style: normal;
  color: $basic-charade;
  letter-spacing: -0.415px;
  background-color: $basic-white;
}
