.nav {
  font-family: $font-deafult;
  font-size: 14px;
  line-height: 17px;
  font-weight: 500;
  font-style: normal;
  text-align: left;
  color: $special-woodsmoke;

  @media (max-width: $tablet-width-only) {
    &--js {
      position: absolute;
      top: 0;
      right: 0;
      z-index: 5;
      width: 100vw;
      height: 100vh;
      padding-top: 137px;
      background-color: $basic-white;
    }

    &--js .nav__list {
      display: flex;
      flex-direction: column;
    }

    &--js .nav__item {
      @include reset-list;
      display: block;
      margin-bottom: 30px;
      padding: 15px 40px 17px 16px;
      font-size: 20px;
      line-height: 28px;
      font-weight: 600;
      color: $special-abbey;
      background-color: $special-soft-peach;
      background-image: url("../img/icon-menu-arrow.svg");
      background-repeat: no-repeat;
      background-position: right 16px center;
    }
  }

  @media (max-width: $mobile-width-only) {
    .nav {
      &--no-js .nav__list {
        display: flex;
        flex-direction: column;
      }

      &--no-js .nav__item {
        @include reset-list;
        display: block;
        width: 100%;
        margin-bottom: 10px;
        padding: 15px 20px 17px 20px;

        background-color: $basic-white;

        text-align: center;
      }
    }
  }
}

.nav__list {
  @include reset-list;
  display: flex;
  flex-wrap: wrap;
}

.nav__item {
  margin-left: 40px;
  color: $special-woodsmoke;

  &:hover,
  &:focus {
    color: $basic-bittersweet;
  }

  &:active {
    opacity: 0.7;
  }
}
