.callback {
  @include wrapper;
  display: flex;
  justify-content: flex-end;
  padding: 70px 0;
  font-family: $font-deafult;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  font-style: normal;
  text-align: center;
  color: $basic-charade;
  background-image: url("../img/bg-callback.jpg");
  background-repeat: no-repeat;
  background-size: auto 100%;
  background-position: 12px -3px;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/bg-callback-2x.jpg");
  }

  @media (max-width: $tablet-width-only) {
    justify-content: center;
    padding: 70px 0 75px 0;
    background-size: cover;
    background-position: 0 0;
  }

  @media (max-width: $mobile-width-only) {
    padding: 0;
    background-image: none;
  }
}

.callback__container {
  width: 656px;
  margin-right: -12px;
  padding: 50px 49px 49px 50px;
  background-color: $basic-white;
  border-radius: 5px;
  box-shadow: 0 4px 30px transparentize($basic-charade, 0.9);

  @media (max-width: $tablet-width-only) {
    margin-right: 0;
  }

  @media (max-width: $mobile-width-only) {
    width: auto;
    padding: 51px 16px 50px 16px;
    background-color: $special-soft-peach;
    box-shadow: none;
  }
}

.callback__title {
  display: block;
  margin-bottom: 11px;
  font-size: 25px;
  line-height: 32px;
  font-weight: 600;

  @media (max-width: $mobile-width-only) {
    margin-bottom: 20px;
    font-size: 25px;
    line-height: 32px;
    font-weight: 600;
  }
}

.callback__text {
  @include reset-list;
  margin-bottom: 24px;

  @media (max-width: $mobile-width-only) {
    margin-bottom: 19px;
  }
}

.callback__form {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.callback__form-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;

  @media (max-width: $mobile-width-only) {
    width: 288px;
  }
}

.callback__input {
  width: 269px;
  margin-bottom: 25px;
  padding: 16px 15px 15px 14px;
  font-family: $font-deafult;
  font-size: 14px;
  line-height: 17px;
  color: $basic-charade;
  border: 1px solid $special-charade-03;

  &-tel {
    padding: 16px 15px 15px 50px;
    background-image: url("../img/input-ph.svg");
    background-repeat: no-repeat;
    background-position: 14px 5px;
    background-size: auto 29px;
  }

  &::placeholder {
    font-family: $font-deafult;
    font-size: 14px;
    line-height: 17;
    font-weight: 400;
    color: $basic-charade;
  }

  &:invalid {
    border: 1px solid $basic-bittersweet;
  }

  &:hover,
  &:focus {
    border: 1px solid $basic-charade;
  }

  @media (max-width: $tablet-width-only) {
    margin-bottom: 20px;
  }

  @media (max-width: $mobile-width-only) {
    width: 100%;
  }
}

.callback__button {
  width: 176px;
  padding: 18px 10px 17px 10px;
  border: 1px solid $basic-white;

  @media (max-width: $tablet-width-only) {
    min-width: 249px;
    padding: 20px 10px 18px 10px;
  }

  @media (max-width: $mobile-width-only) {
    width: 288px;
  }
}
