.banner {
  font-family: $font-deafult;
  font-size: 16px;
  line-height: 27px;
  letter-spacing: -0.47px;
  font-weight: 400;
  font-style: normal;
  text-align: left;
  color: $basic-white;
  display: flex;
  flex-direction: column;
  justify-content: left;
  min-height: 860px;
  background-image: url("../img/bg-banner.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/bg-banner-2x.jpg");
  }

  @media (max-width: $tablet-width-only) {
    min-height: 344px;
    padding: 60px 185px 30px 185px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
  }

  @media (max-width: $mobile-width-only) {
    padding: 82px 16px 30px 16px;

    background-color: transparent;
    background-size: auto 235px;
    background-position: center 0;
  }
}

.banner__wrapper {
  @include wrapper;
  padding: 185px 80px 30px 700px;

  @media (max-width: $desktop-width-only) {
    padding: 185px 80px 30px 550px;
    text-align: center;
  }

  @media (max-width: $tablet-width-only) {
    display: flex;
    flex-direction: column;
    text-align: center;
    padding: 0;
  }

  @media (max-width: $mobile-width-only) {
    width: auto;
  }
}

.banner__title {
  @include reset-list;
  margin-bottom: 29px;
  font-size: 55px;
  line-height: 80px;
  font-weight: 600;
  letter-spacing: -1.11px;

  @media (max-width: $tablet-width-only) {
    margin-bottom: 15px;
    font-size: 30px;
    line-height: 45px;
    letter-spacing: 0;
  }

  @media (max-width: $mobile-width-only) {
    min-height: 72px;
    margin-bottom: 101px;
    margin-left: 6px;

    font-size: 24px;
    line-height: 36px;
  }
}

.banner__text {
  @include reset-list;
  margin-bottom: 33px;

  @media (max-width: $tablet-width-only) {
    margin-bottom: 11px;
  }

  @media (max-width: $mobile-width-only) {
    margin-bottom: 20px;
    color: $basic-charade;
  }
}

.banner__button {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 340px;
  padding: 30px 39px 29px 40px;

  @media (max-width: $tablet-width-only) {
    align-self: center;
    width: 250px;
    padding: 19px 39px 16px 40px;
  }

  @media (max-width: $mobile-width-only) {
    width: 288px;
  }
}

.banner__icon {
  align-self: center;
  fill: none;
  stroke: $basic-white;
}
