.country-list {
  @include reset-list;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 902px;
  font-family: $font-deafult;
  font-size: 20px;
  line-height: 24px;
  font-weight: 500;
  font-style: normal;
  text-align: left;
  color: transparentize($basic-charade, 0.7);
  letter-spacing: -0.415px;
  border-bottom: 1px solid transparentize($basic-charade, 0.7);
}

.country-list__item {
  margin-right: 100px;

  &:last-child {
    margin-right: 0;
  }
}

.country-list__link {
  display: block;
  padding: 15px 0;
  color: transparentize($basic-charade, 0.7);

  &:hover,
  &:focus {
    color: $basic-bittersweet;
  }

  &--active {
    color: $basic-bittersweet;
  }
}
