.prices {
  @include wrapper;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-bottom: 138px;
  font-size: 16px;
  line-height: 27px;
  font-weight: 400;
  font-style: normal;
  text-align: left;
  color: transparentize($basic-charade, 0.5);

  @media (max-width: $tablet-width-only) {
    flex-direction: column;
    padding-bottom: 125px;
  }

  @media (max-width: $mobile-width-only) {
    padding-bottom: 49px;
  }
}
