.country-description {
  min-height: 636px;
  display: grid;
  grid-template-columns: 40% 60%;
  font-family: $font-deafult;
  font-size: 16px;
  line-height: 28px;
  font-weight: 400;
  letter-spacing: -0.47px;
  font-style: normal;
  text-align: left;
  color: transparentize($basic-charade, 0.3);

  &--hidden {
    display: none;
  }

  @media (max-width: $tablet-width-only) {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;

    &--hidden {
      display: none;
    }
  }
}

.country-description__wrapper {
  @media (max-width: $tablet-width-only) {
    margin-bottom: 31px;
  }

  @media (max-width: $mobile-width-only) {
    margin-bottom: 51px;
  }
}

.country-description__title {
  @include reset-list;
  position: relative;
  margin-top: 15px;
  margin-bottom: 52px;
  width: 360px;
  font-size: 55px;
  line-height: 80px;
  letter-spacing: -1.25px;
  font-weight: 600;
  font-style: normal;
  text-align: left;
  color: $basic-charade;

  &::after {
    content: "";
    position: absolute;
    bottom: -20px;
    left: 0;
    width: 175px;
    height: 3px;
    background-color: $basic-bittersweet;
  }

  @media (max-width: $tablet-width-only) {
    width: auto;
    margin-bottom: 22px;
    font-size: 30px;
    line-height: 45px;
    letter-spacing: 0;
    text-align: center;

    &::after {
      content: "";
      display: none;
    }
  }

  @media (max-width: $mobile-width-only) {
    font-size: 24px;
    line-height: 36px;
    margin-top: 7px;
    margin-bottom: 19px;
  }
}

.country-description__text {
  @include reset-list;
  margin-bottom: 48px;
  width: 370px;
  text-align: left;

  @media (max-width: $tablet-width-only) {
    margin-bottom: 20px;
    width: auto;
    text-align: center;
    padding: 0 21px;
  }
}

.country-description__button {
  width: 250px;
  padding: 30px;

  @media (max-width: $tablet-width-only) {
    margin: 0 auto;
    width: 250px;
    padding: 19px 39px 16px 40px;
    font-size: 10px;
    line-height: 12px;
    font-weight: 500;
    letter-spacing: 2px;
  }

  @media (max-width: $mobile-width-only) {
    width: 288px;
  }
}
