.description {
  @include wrapper;
  display: flex;
  flex-direction: column;
  position: relative;
  background-color: transparent;

  &::after {
    content: "";
    position: absolute;
    top: 170px;
    left: -147px;
    z-index: -1;
    width: 1444px;
    height: 1089px;
    background-size: cover;
    background-image: url("../img/bg-map.svg");
    background-repeat: no-repeat;

    @media (max-width: 1400px) {
      display: none;
    }
  }
}


.description__list-link {
  margin-bottom: 81px;

  @media (max-width: $desktop-width-only) {
    margin-bottom: 17px;
    margin-left: 37px;
    overflow-x: scroll;
  }
}
