.button {
  display: block;
  box-sizing: border-box;
  padding: 30px 40px 30px 40px;
  width: 100%;
  max-width: 340px;
  font-family: $font-deafult;
  font-size: 12px;
  font-weight: 700;
  line-height: 15px;
  letter-spacing: 2px;
  color: $basic-white;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  background-color: $basic-bittersweet;
  cursor: pointer;

  &:hover {
    color: $basic-white;
    background-color: $special-jaffa;
  }

  &:focus {
    color: $basic-white;
    background-color: $special-jaffa;
  }

  @media (max-width: $tablet-width-only) {
    width: 250px;
    padding: 19px 39px 16px 40px;
    font-size: 10px;
    line-height: 12px;
    font-weight: 500;
    letter-spacing: 2px;
  }
}
